@import "@/styles/main.scss";












































































































































































































































































































































































































































@import '../styles/mixins';
@import '../styles/colors';
@import '../styles/animations';
@import '../styles/icons';

.sweet-modal-overlay {
	position: fixed;
	top: 0;
	left: 0;

	width: 100vw;
	height: 100vh;

	z-index: 9001;

	font-size: 14px;

	-webkit-font-smoothing: antialiased;

	// Theming
	background: rgba(#fff, 0.9);

	&.theme-dark {
		$color: color(dark-overlay);

		// background: radial-gradient(ellipse at center, rgba($color, 0.9) 0%, rgba($color, 0.96) 100%);
		background: rgba($color, 0.94);
	}

	// Animation
	opacity: 0;
	transition: opacity 0.3s;
	transform: translate3D(0, 0, 0);
	-webkit-perspective: 500px;

	&.is-visible {
		opacity: 1;
	}
}

.sweet-modal {
	@include border-box;

	background: #fff;
	box-shadow: 0px 8px 46px rgba(#000, 0.08),
				0px 2px  6px rgba(#000, 0.03);

	position: absolute;
	top: 50%;
	left: 50%;
	// transform: translate(-50%, -50%); // Done by the animation later

	width: 80%;
	max-width: 640px;
	max-height: 100vh;
	overflow-y: auto;

	border-radius: 2px;

	.sweet-box-actions {
		position: absolute;
		top: 12px;
		right: 12px;

		.sweet-action-close {
			display: inline-block;
			cursor: pointer;

			color: color(dark);
			text-align: center;

			width: 42px;
			height: 42px;
			line-height: 42px;

			border-radius: 50%;

			svg {
				width: 24px;
				height: 24px;

				vertical-align: middle;
				margin-top: -2px;

				@include svg-path(true) {
					fill: currentColor;
				}
			}

			&:hover {
				background: color(blue);
				color: #fff;
			}
		}
	}

	.sweet-title {
		@include ellipsis;

		height: 64px;
		line-height: 64px;

		border-bottom: 1px solid color(border);

		padding: {
			left: 32px;
			right: 64px;
		}

		> h2 {
			@include ellipsis;
			@include mp0;

			font-weight: 500;
			font-size: 22px;
		}
	}

	ul.sweet-modal-tabs {
		@include ulreset;

		display: flex;
		align-items: center;
		width: calc(100% + 32px);
		height: 100%;

		margin-left: -32px;
		overflow-x: auto;

		li.sweet-modal-tab {
			display: block;
			height: 100%;

			a {
				@include ellipsis;

				display: flex;
				align-items: center;

				padding: {
					left: 20px;
					right: 20px;
				}

				color: color(dark);
				text-decoration: none;
				text-align: center;

				height: 100%;

				span.sweet-modal-tab-title {
					display: block;
				}

				span.sweet-modal-tab-icon {
					display: block;
					line-height: 1.0;

					svg, img {
						width: 16px;
						height: 16px;

						@include svg-path(true) {
							fill: currentColor;
						}
					}
				}

				span.sweet-modal-tab-icon + span.sweet-modal-tab-title {
					line-height: 1.0;
					margin-top: 8px;
				}
			}

			&:first-child a {
				padding-left: 32px;
			}

			&.active a {
				font-weight: 600;
				color: color(blue);
			}

			&.disabled a {
				@include unclickable;
				color: color(light-grey);
			}
		}
	}

	&.has-tabs:not(.has-title) {

		.sweet-title {
			height: 84px;
			line-height: 84px;
		}
	}

	&.has-tabs.has-title {

		ul.sweet-modal-tabs {
			width: 100%;
			height: 48px;

			margin: 0;
			border-bottom: 1px solid color(border);

			li.sweet-modal-tab a {
				margin-top: -4px;

				span.sweet-modal-tab-icon {
					display: inline-block;

					svg, img {
						vertical-align: middle;

						margin: {
							top: -2px;
							right: 8px;
						}
					}
				}

				span.sweet-modal-tab-title {
					display: inline-block;
				}
			}
		}
	}

	.sweet-content {
		display: flex;
		align-items: center;

		padding: {
			left: 32px;
			right: 32px;
			top: 24px;
			bottom: 24px;
		}

		line-height: 1.5;

		.sweet-content-content {
			flex-grow: 1;
		}

		.sweet-modal-tab:not(.active) {
			display: none;
		}

		.sweet-modal-icon {
			margin-bottom: 36px;
		}
	}

	.sweet-buttons {
		text-align: right;

		padding: {
			left: 20px;
			right: 20px;
			top: 12px;
			bottom: 12px;
		}
	}

	.sweet-content + .sweet-buttons {
		border-top: 1px solid color(border);
	}

	// Special Styles
	&.is-alert {

		.sweet-content {
			display: block;

			text-align: center;
			font-size: 16px;

			padding: {
				top: 64px;
				bottom: 64px;
			}
		}
	}

	&.has-tabs.has-icon .sweet-content {
		padding: {
			top: 32px;
			bottom: 32px;
		}

		.sweet-content-content {
			padding-left: 32px;
		}

		.sweet-modal-icon {
			margin-bottom: 0;
		}
	}

	&:not(.has-content) {

		.sweet-modal-icon {
			margin-bottom: 0;
		}
	}

	// Theming
	&.theme-dark {
		$color: color(dark-modal);

		background: $color;
		color: #fff;

		.sweet-box-actions {

			.sweet-action-close {
				color: #fff;
			}
		}

		.sweet-title {
			border-bottom-color: darken($color, 8%);
			box-shadow: 0px 1px 0px lighten($color, 8%);
		}

		ul.sweet-modal-tabs li {

			a {
				color: #fff;
			}

			&.active a {
				color: color(blue);
			}

			&.disabled a {
				color: lighten($color, 20%);
			}
		}

		&.has-tabs.has-title {

			ul.sweet-modal-tabs {
				border-bottom-color: darken($color, 8%);
				box-shadow: 0px 1px 0px lighten($color, 8%);
			}
		}

		.sweet-content + .sweet-buttons {
			border-top-color: lighten($color, 8%);
			box-shadow: 0px -1px 0px darken($color, 8%);
		}
	}

	// Animation
	transform: scale(0.9) translate(calc(-50% - 32px), -50%);
	opacity: 0;

	transition: {
		property: transform, opacity;
		duration: 0.3s;
		delay: 0.05s;
		timing-function: cubic-bezier(0.52, 0.02, 0.19, 1.02);
	}

	.sweet-buttons,
	.sweet-content {
		opacity: 0;

		transition: {
			property: transform, opacity;
			duration: 0.3s;
			delay: 0.09s;
			timing-function: cubic-bezier(0.52, 0.02, 0.19, 1.02);
		}
	}

	.sweet-content {
		transform: translateY(-8px);
	}

	.sweet-buttons {
		transform: translateY(16px);
	}

	&.is-visible {
		transform: translate(-50%, -50%);
		opacity: 1;

		.sweet-buttons,
		.sweet-content {
			transform: none;
			opacity: 1;
		}
	}

	&.bounce {
		animation-name: bounce;
		animation-duration: 0.3s;
		animation-iteration-count: 2;
		animation-direction: alternate;
	}

	// Responsiveness
	@include media(desktop) {
		// Bouncing animation
		@keyframes bounce {
			0% {
				transform: scale(1) translate(-50%, -50%);
			}

			50% {
				transform: scale(1.02) translate(calc(-50% + 8px), -50%);
			}

			100% {
				transform: scale(1) translate(-50%, -50%);
			}
		}
	}

	&.is-mobile-fullscreen {
		
		@include media(mobile) {

			& {
				width: 100%;
				height: 100vh;

				left: 0;
				top: 0;

				transform: scale(0.9);

				&.is-visible {
					transform: none;
				}
			}

			.sweet-buttons {
				@include border-box;

				position: absolute;
				bottom: 0;
				left: 0;

				width: 100%;
			}
		}
	}
}
